.array-container {
    width: 100%;
    text-align: center;
    
}

.arraybar {
    display: inline-block;
    margin: 0px 1px;
}

.arraybar-container{
    width : 95%;
    display: inline-block;
    margin-top: 10px;
    padding-top: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

.slider-container{
    grid-column-start: 2;
    grid-column-end: 3;
    height: 100px;
    padding-top: 10px;
}

.infoBar-container{
    background-color: rgb(127, 130, 131);
    width: 95%;
    height: 60px;
    margin-top: 10px;
    display: inline-grid; 
    grid-template-columns: 80px 200px 500px 100px 80;
    grid-column-gap: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.button-container{
    grid-column-start: 3;
    grid-column-end: 4;
}
.grap-container{
    grid-column-start: 4;
    grid-column-end: 5;
}

label{
    font-size: large;
}

button{
  margin-right: 7px;
  margin-left:  7px;
  width : 150px;
  border-radius: 7px;
  font-size: 16px;
  font-weight: bold;
  margin-top: 7px;
}

button:hover{
  box-shadow :0px 15px 25px -5px rgba(darken(dodgerblue, 40%));
  transform :scale(1.1);
}
button:active{
  box-shadow :0px 4px 8px rgba(darken(dodgerblue, 30%));
  transform :scale(.90);
}


.temp{
  grid-column-start: 5;
  grid-column-end: 6;
  position :relative;
  right : 20px;
}

